
*
{
  padding: 0;
  margin: 0;
  font-family: "Margarine", system-ui;
  font-weight: 700;
  font-style: normal;
  
}
.bg
{
  background: linear-gradient(rgba(117, 116, 116, 0.5), rgba(100, 98, 98, 0.5)),
  url(https://png.pngtree.com/thumb_back/fh260/background/20230930/pngtree-illustrated-3d-render-of-internet-search-bar-with-magnifier-icon-searching-image_13512460.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
  position: fixed;
}
.input-container
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.input-container h1
{
  color: white;
  font-size: 55px;
}
/* .input-container */
.input-container .mid-input-box
{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.input-container .mid-input-box input
{
  background-color: transparent;
  border: 2px solid red;
  text-align: left;
  padding: 10px;;
  font-size: 20px;
  height: 30px;
  border-radius: 10px;
  color: white;
}
.input-container .mid-input-box input:hover
{
  border: 2px solid orangered;
}
.input-container .mid-input-box input::placeholder
{
  color: gray;
}
.input-container .mid-input-box input:focus
{
  border: 2px solid green;
  outline: none;
}
.input-container .mid-input-box button
{
  background-color: red;
  color: white;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  font-size: 20px;
  height: 50px;
  border-radius: 10px;
}
.input-container .mid-input-box button:hover
{
  background-color: orangered;
}
.input-container .mid-input-box button:active
{
  background-color: green;
}


/* Navbar */
nav
{
  background-color: transparent;
  /* backdrop-filter: blur(10px); */
  padding: 15px 3%;
  margin-top: 50px;
}
nav ul
{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav ul li
{
  color: white;
  cursor: pointer;
  font-size: 22px;
  list-style-type: none;
  border-bottom: 3px solid transparent;
}
nav ul li:hover
{
  border-bottom: 3px solid white;
  transition: 1s;
}
.navActive
{
  color: red;
  transform: scale(1.1);
  border-bottom: 3px solid red;
}
.navActive:hover
{
  border-bottom: 3px solid red;
}


/* section */
h4
{
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.section
{
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}